@import '../../_pallet';
#throneroom {
    @include flexColumn;
    > #tr-player {
      @include flexColumn;
      > p {
        align-self: flex-start; } }
    > .back-button {
      position: absolute;
      top: 2rem;
      left: 1rem; }
    > section {
        display: flex;
        flex-direction: column;
        align-self: center;
        > h4 {
            align-self: center; }
        > .loader {
            align-self: center; } } }

@media (max-width: 1200px) {
   #throneroom {
     > .back-button {
      top: 1rem;
      left: 1rem; }
     > h1 {
       font-size: 20px; } } }
