#main-menu {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
    > section > div.icon-button {
        margin: 5px;
        > img {
            position: relative;
            top: 5px; } }
    > .log-out-in {
        position: absolute;
        top: 5%;
        right: 5%;
        border-radius: 100%;
        width: 3rem;
        height: 3rem;
        > img {
            position: relative;
            width: 2rem;
            height: 2rem;
            color: white; }
        > img.img-out {
            right: 4px;
            top: 2px; }
        > img.img-in {
            right: 6px;
            top: 1px; } }

    > div.Logo {
        position: absolute;
        top: 4.5rem;
        > h1 {
          margin: 0;
          font-size: 3rem; }
        > p {
          margin: 0;
          font-size: 1.5rem; } }
    > div.main {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
        > .normal-button {
            margin: 8px 0;
            padding: 5px 15px;
            font-size: 18px; } }
    #menu-nav {
        position: absolute;
        bottom: 1rem; } }
