@import '../../_pallet';
#sp-mode {
    height: 100vh;
    @include flexColumn;
    > h1 {
        position: absolute;
        top: .1rem; }
    > .back-button {
        position: absolute;
        top: 2rem;
        left: 2rem; }
    > section {
        display: flex;
        flex-direction: column;
        width: 15vw;
        > button.normal-button {
          margin: 10px;
          padding: 20px 40px; } } }


#sp-tribe {
   @include flexColumn;
   height: 100vh;
   > #sp-tribe-random {
       @include flexColumn; }
   > section > div {
       display: flex;
       justify-content: center;
       align-content: center;
       align-items: center;
       > .icon-button {
           margin: 5px; } }
   > h1 {
        position: absolute;
        top: .1rem; }
   > .back-button {
        position: absolute;
        top: 1rem;
        left: 1rem; }
   > #tribe-data {
        @include flexColumn;
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background: $darkgray_bg;
        > p {
            text-align: center; }
        > h4 {
            position: absolute;
            top: 2rem;
            left: 2rem;
            font-size: 2rem; }
        > img {
            width: 10rem;
            height: 10rem;
            margin: 20px; }
        > div {
            width: 20rem;
            display: flex;
            align-items: center;
            justify-content: space-around;
            > button.cancel {
               color: lightgray;
               background: gray; } } } }

#sp-setup {
    @include flexColumn;
    > .back-button {
        position: absolute;
        top: 1rem;
        left: 1rem; }
    > section {
        padding: .6rem 0;
        margin: 1rem 0;
        background: $darkgray_bg;
        @include flexColumn;
        > h5 {
            user-select: none;
            margin-top: 0; } } }

.scrollmenu-item {
        padding: 0 10px;
        margin: 5px 10px;
        user-select: none;
        cursor: pointer;
        border: 1px transparent solid;
        outline: none; }
.menu-item-wrapper.active > .scrollmenu-item {
  background: white;
  color: black; }


@media (max-width: 1350px) {
    #sp-mode {
        > h1 {
            font-size: 20px; }
        >section {
            width: 20rem;
            > button.normal-button {
              padding: 10px 20px; } } }
    #sp-tribe {
        > h1 {
            font-size: 20px; } }
    #sp-setup {
        > h1 {
            font-size: 20px; }
        > .normal-button {
            position: absolute;
            bottom: 2rem; } } }

