@font-face {
    font-family: 'Polytopia';
    src: url('../../assets/AdequateLight.ttf'); }
@import "./theme";

html,body {
 height: 100vh;
 margin: 0;
 background: #000000;
 overflow: hidden;
 font-family: "Polytopia";
 color: white; }

.background {
    position: absolute;
    background: url('../../assets/images/bg_menu.webp') no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    z-index: 0; }

.so-card-body {
    font-family: 'Polytopia';
    color: black;
    text-align: center; }

.menu-item-wrapper {
    outline: none; }

.so-card {
    background: rgba(36,38,36,0.75);
    opacity: 85%;
    border: none;
    > .so-card-body {
        background: rgba(36,38,36,0.75);
        color: white; }
    > .so-card-footer {
        background: rgba(36,38,36,0.75);
        border-top: none; }
    > a.so-modal-close {
        display: none; } }


