@import '../_pallet.sass';
#view-block {
    position: absolute;
    width: 472px;
    bottom: 0;
    left: 38vw;
    @include flexColumn;
    > .view-scroll {
        display: inline;
        height: 81px;
        width: 472px;
        background: black; }
    > section {
        background: $darkgray_bg;
        height: 63px;
        width: 472px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-content: center;
        align-items: center;
        > img {
            height: 50px;
            width: 50px;
            margin-left: 10px; }
        > div {
            position: relative;
            text-align: left;

            top: -6px;
            display: flex;
            flex-direction: column;
            > h3 {
                font-size: 14px;
                padding: 0;
                margin: 0; }
            > p {
                font-size: 12px;
                padding: 0;
                margin: 0; } }
        > button {
                display: flex;
                justify-content: center;
                align-content: center;
                align-items:  center;
                border-radius: 100%;
                width: 40px;
                height: 40px;
                text-align: center;
                margin-right: 10px;
                border: none;
                background: #a5a5a5;
                > img {
                    width: 1.5rem;
                    height: 1.5rem; } } } }
#nav-game {
    position: absolute;
    bottom: 10px;
    left: 40%;
    > div {
       margin-left: 20px; } }
#game-data {
    position: absolute;
    top: 0;
    left: 41%;
    display: flex;
    > section {
        margin-left: 10px;
        > p {
            text-align: center; } } }

#tech-tree {
    position: absolute;
    background: black;
    z-index: 1000;
    width: 100%;
    height: 100%; }
