@import '../Pallet';
.back-button {
    color: black;
    font-weight: bold;
    border-radius: 100%;
    border: none;
    width: 2.2rem;
    height: 2.2rem;
    background: rgba(209,209,209,0.7);
    text-align: center;
    font-size: 25px;
    outline: none;
    @include flexColumn;
    > img {
        position: relative;
        right: 2px;
        width: 20px;
        height: 20px; } }
.back-button:active {
    transform: scale(1.1); }
.back-button:hover {
    transform: scale(1.1); }
