@import '../../_pallet';
#multiplayer {
    @include flexColumn;
    justify-content: flex-start;
    height: 100vh;
    > .back-button {
        position: absolute;
        top: 2rem;
        left: 2rem; }
    > .saved {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        align-content: center;
        background: $blue_one_HEX;
        border-radius: 10px;
        padding: 10px 30px;
        margin: 10px;
        border: none;
        color: white;
        font-family: 'Polytopia';
        > h5 {
            margin: 0;
            margin-left: 15px; }
        > img {
            width: 20px;
            height: 20px; } }
    > .saved:active {
        transform: scale(1.2); }
    > .saved:hover {
        transform: scale(1.2); }
    > section {
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: absolute;
        bottom: 1rem;
        > .icon-button {
            margin: 10px; } } }
.alert-nologin {}

.so-alert-icon {
    align-self: center; }



