@import '../../_pallet.sass';
#highscore {
    @include flexColumn;
    > .back-button {
      position: absolute;
      top: 2rem;
      left: 1rem; } }
.menu-item {
  padding: 0 40px;
  margin: 5px 10px;
  user-select: none;
  cursor: pointer;
  border: 1px transparent solid;
  index-z: 1; }
.menu-item-wrapper {
  user-select: none;
  border: 1px transparent solid; }
.hr-menu {
    background: $darkgray_bg; }


@media (max-width: 1200px) {
   #highscore {
     > h1 {
       font-size: 20px; }
     > .back-button {
      top: 1rem;
      left: 1rem; } } }






